body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #606060FF;
  color: white;
  box-shadow: inset 0 0 10px rgba(0,0,0,0.5); 
  height: 100vh; 
  width: 100vw; 
  margin: 0; 
  padding: 0; 
  border-radius: 20px;
  font-family: "Roboto", sans-serif;
}

h1 {
  text-align: center;
  font-size: 2rem; 
  color: #D6ED17FF;
  margin-top: 30px; 
  padding-top: 10px; 
  padding-bottom: 10px; 
  padding-left: 10px; 
  padding-right: 10px; 
  transition: all 0.3s ease-in-out;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 1.2rem; 
    padding: 10px 15px; 
  }
}


.board {
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-template-rows: 100px 100px 100px;
  max-width: 600px;  max-width: 600px;
  cursor: pointer;
  position: relative;
}


.tile {
  color: #D6ED17FF;
  font-size: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
}


.right-border {
  border-right: 0.2em solid #D198C5FF;
}

.bottom-border {
  border-bottom: 0.2em solid #D198C5FF;
}

.x-hover:hover::after {
  content: "X";
  opacity: 0.4;
  color: #D6ED17FF
}

.o-hover:hover::after {
  content: "O";
  opacity: 0.4;
  color: #D6ED17FF
}

.strike {
  position: absolute;
  background-color: #E0C568FF;
}

.strike-row-1 {
  width: 100%;
  height: 4px;
  top: 15%;
}
.strike-row-2 {
  width: 100%;
  height: 4px;
  top: 48%;
}
.strike-row-3 {
  width: 100%;
  height: 4px;
  top: 83%;
}
.strike-column-1 {
  height: 100%;
  width: 4px;
  left: 15%;
}
.strike-column-2 {
  height: 100%;
  width: 4px;
  left: 48%;
}
.strike-column-3 {
  height: 100%;
  width: 4px;
  left: 83%;
}
.strike-diagonal-1 {
  width: 90%;
  height: 4px;
  top: 50%;
  left: 5%;
  transform: skewY(45deg);
}
.strike-diagonal-2 {
  width: 90%;
  height: 4px;
  top: 50%;
  left: 5%;
  transform: skewY(-45deg);
}

.game-over {
  color: #D6ED17FF;
  text-align: center;
  border: 2px solid #D198C5FF; 
  border-radius: 15px; 
  padding: 10px;
  margin-top: 50px;
  font-size: 1.5em;

  box-shadow: 0 0 10px #D198C5FF, 
  inset 0 0 10px #D198C5FF;

  transition: border-color 0.3s ease; 
}



.reset-button {
  margin-top: 50px;
  padding: 10px;
  background-color: #5CC8D7FF;
  color: #D6ED17FF;
  width: 100%;
  font-size: 1.5em;
  border: 2px solid #D198C5FF; 
  border-radius: 15px; 
  box-shadow: 0 0 10px #D198C5FF; 
  
  transition: background-color 0.3s ease; 
}

.reset-button:hover {
  background-color: #D198C5FF; 
  cursor: pointer
}


